import { NgModule } from '@angular/core';
import { CommonModule } from '@angular/common';
import { FormsModule, ReactiveFormsModule } from '@angular/forms';


import { SharedModule } from '../shared/shared.module';
import { LoggerComponent } from './logger.component';

// 3rd party
import { NgxPageScrollModule } from 'ngx-page-scroll';
import { FontAwesomeModule } from '@fortawesome/angular-fontawesome';
import { LoggerMainComponent } from './logger-main/logger-main.component';
import { LoggerSpecsComponent } from './logger-specs/logger-specs.component';
import { LoggerMoreComponent } from './logger-more/logger-more.component';
import {RouterModule, Routes} from "@angular/router";
import {NgwWowModule} from "ngx-wow";


// Routes
const routes: Routes = [
  {
    path: '',
    component: LoggerComponent
  }
];

@NgModule({
  declarations: [LoggerComponent, LoggerMainComponent, LoggerSpecsComponent, LoggerMoreComponent],
  imports: [
    CommonModule,
    RouterModule.forChild(routes),
    SharedModule,
    ReactiveFormsModule,
    FormsModule,
    // 3rd party
    NgxPageScrollModule,
    FontAwesomeModule,
    NgwWowModule
  ]
})
export class LoggerModule { }
