import { Component, OnInit } from '@angular/core';
import { Ci18nSpec } from 'src/app/shared/models/ci18n-spec';

import { TranslateService } from '@ngx-translate/core';

@Component({
  selector: 'app-logger-specs',
  templateUrl: './logger-specs.component.html',
  styleUrls: ['./logger-specs.component.scss']
})
export class LoggerSpecsComponent implements OnInit {

  public bError = false;

  public specs: Ci18nSpec[] = [{
    IMG: {
        ALT: '',
        SRC: ''
    },
    TEXT: 'Loading'
}];

  constructor(public translate: TranslateService) { }

  ngOnInit() {

    this.translate.get('LOGGER.SPECS.SPEC').subscribe((res: Ci18nSpec[] | string) => {
      if (typeof(res) === 'string') {
          this.bError = true;
          this.specs = [{
              TEXT: 'Cannot get translation',
              IMG: {
                  ALT: '',
                  SRC: ''
              }
          }];
      } else {
          this.specs = res as Ci18nSpec[];
      }
    });

  }

}
